// ===== hero ===== 

@if 1 {
  .mb-160 {
    @include adaptive("margin-bottom", 160, 80, 1);
  }

  .hero {
    flex-shrink: 0;
    position: relative;
    padding: 10px 0;
  }

  .hero__top {
    @include adaptive("margin-bottom", 80, 40, 1);
  }

  .hero__title {
    & span {
      color: var(--color-primary);
    }
  }

  .hero__link {
    position: relative;
    bottom: 30px;
    border-radius: 30px;
    text-transform: none;
    font-weight: 500;
    margin-top: 40px;
    width: 100%;

    @media (min-width: $md) {
      position: relative;
      bottom: 21px;
      margin-top: 0;
      width: auto;
    }
  }

  .hero__container {
    @media (min-width: $xl) {
      padding: 0;
    }
  }

  .hero__wrap {
    position: relative;

  }

  .parallax-window {
    aspect-ratio: 1/0.8;
    background: transparent;
    border-radius: 10px;

    @media (min-width: $md) {
      aspect-ratio: 1/0.6;
    }

    @media (min-width: $xl) {
      aspect-ratio: 1/0.42;
    }

    & img {
      border-radius: 10px;
    }
  }

  .parallax-mirror {
    border-radius: 10px;
  }

  .hero__wrap-contact {
    margin-top: 15px;

    @media (min-width: $md) {
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      padding: 40px;
      margin-top: 0px;
    }
  }

  .contact {
    position: sticky;
    top: 40px;
    max-width: 530px;
    margin: 0 auto;
    padding: 15px;
    font-family: var(--font-primary);
    border-radius: 10px;
    background-color: #fff;

    @media (min-width: $md) {
      padding: 60px;
    }
  }

  .contact__text {
    margin-bottom: 10px;
    line-height: 1.3;
    font-size: calc(18rem / 16);
    font-weight: 500;

    & span {
      color: var(--color-primary);
    }

    @media (min-width: $md) {
      margin-bottom: 30px;
      font-size: calc(36rem / 16);
    }
  }

}



// ===== buns ===== 

@if 1 {
  .title__row {
    margin-bottom: 40px;

    @media (min-width: $md) {
      margin-bottom: 60px;
    }
  }

  .subtitle {
    font-family: var(--font-primary);
    font-size: calc(14rem / 16);
    opacity: 0.5;
    margin-bottom: 10px;

    @media (min-width: $md) {
      font-size: calc(20rem / 16);
    }
  }

  .buns__title {
    span {
      color: var(--color-primary);
    }
  }

  .subtitle__col {
    @media (min-width: $md) {
      flex: 0 0 41.666667%;
    }
  }

  .title__col {
    flex: 1 0 0%;
  }


  .buns__col {
    margin-bottom: 10px;
    flex: 0 0 50%;

    @media (min-width: $md) {
      flex: 0 0 50%;
      margin-bottom: 20px;
    }

    @media (min-width: $lg) {
      flex: 0 0 25%;
    }
  }

  .buns__item {
    height: 100%;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;

    @media (min-width: $xl) {
      padding: 30px;
    }

    @media (min-width: $xxl) {
      padding: 40px;
    }

    @media (pointer: fine) {
      &:hover .buns__icon {
        background-color: #003253;
      }
    }
  }

  .buns__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    @include adaptive("margin-bottom", 80, 40, 1);
    color: #fff;
    font-size: 16px;
    border-radius: 10px;
    width: 40px;
    height: 40px;
    background-color: var(--color-primary);
    transition: .2s ease 0s;

    @media (min-width: $md) {
      width: 80px;
      height: 80px;
      font-size: 27px;
    }

    & img {
      width: 17px;
      height: 17px;

      @media (min-width: $md) {
        width: 30px;
        height: 30px;
      }
    }
  }

  .buns__item-title {
    font-family: var(--font-primary);
    font-weight: 500;
    font-size: calc(18rem / 16);
    line-height: 130%;
    margin-bottom: 10px;

    @media (min-width: $md) {
      font-size: calc(36rem / 16);
    }
  }
}



// ===== service ===== 

@if 1 {
  .service {
    @include adaptive("padding-top", 160, 80, 1);
    @include adaptive("padding-bottom", 160, 80, 1);
    color: #fff;
    background-color: #003253;
    border-radius: 10px;

    & p {
      opacity: 0.5;
    }
  }

  .service__col-title {
    align-self: start;

    @media (min-width: $md) {
      flex: 0 0 33.333333%;
      position: sticky;
      top: 160px;
    }
  }

  .service__title {
    margin-bottom: 40px;

    & span {
      color: #4C90CD;
    }
  }

  .service__col {
    @media (min-width: $md) {
      flex: 0 0 66.66667%;
    }

    @media (min-width: $xl) {
      flex: 0 0 58.333333%;
      margin-left: 8.333333%;
    }

    & ol {
      counter-reset: num;
      list-style: none;

      li {
        position: relative;
        padding-left: 15px;

        @media (min-width: $md) {
          padding-left: 50px;
        }
      }

      li::before {
        content: counter(num, decimal-leading-zero)" /";
        counter-increment: num;
        position: absolute;
        left: 0;
        top: -3px;
        opacity: 0.5;
        font-size: calc(14rem / 16);
        font-family: var(--font-primary);

        @media (min-width: $md) {
          font-size: calc(20rem / 16);
        }
      }
    }

  }

  .service__item {
    display: flex;
    transition: .2s ease 0s;

    &:not(:last-child) {
      @include adaptive("margin-bottom", 80, 30, 1);
    }

    @media (pointer: fine) {
      .service__btn {
        opacity: 0;
        display: inline-flex;
      }

      &:hover .service__img img {
        border-radius: 10px;
      }

      &:hover .service__btn {
        opacity: 1;
      }
    }

    & p {
      margin-bottom: 10px;

      @media (min-width: $md) {
        font-size: calc(16rem / 16);
        margin-bottom: 20px;
      }

      @media (min-width: $xl) {
        font-size: calc(20rem / 16);
      }
    }
  }

  .service__img {
    flex: 0 0 31%;

    & img {
      border-radius: 50%;
      aspect-ratio: 1/1;
      transition: .2s ease 0s;
      object-fit: cover;
    }
  }

  .service__info {
    padding: 10px 0px 0px 15px;

    @media (min-width: 1400px) {
      padding: 20px 20px 20px 60px;
    }
  }

  .service__item-title {
    margin-bottom: 5px;
    @include adaptiveRem("font-size", 62, 18, 1);
  }

  .service__cost {
    display: inline-flex;
    align-items: center;
    border-radius: 30px;
    padding: 5px 15px;
    min-height: 37px;
    margin-bottom: 10px;
    font-size: calc(14rem / 16);
    font-family: var(--font-primary);
    background-color: rgba(255, 255, 255, 0.1);

    @media (min-width: $md) {
      padding: 5px 24px;
      min-height: 60px;
      font-size: calc(18rem / 16);
    }

    @media (min-width: $xl) {
      font-size: calc(20rem / 16);

    }
  }

  .service__btn {
    background-color: #3973E1;
    border-radius: 30px;
    transition: .2s ease 0s;
    transition: .2s ease 0s;
    display: none;

    @media (pointer: fine) {
      &:hover {
        background-color: #4C90CD;
      }
    }
  }

}



// ===== review ===== 

@if 1 {
  .review__img {
    aspect-ratio: 1/0.72;
    border-radius: 10px;
    margin-bottom: 10px;

    @media (min-width: $md) {
      aspect-ratio: 1/0.4;
      margin-bottom: 20px;
    }

    & img {
      border-radius: 10px;
    }
  }

  .review__row {
    background-color: #EFF1F1;
  }

  .review__col-title {
    margin-bottom: 10px;

    @media (min-width: $md) {
      flex: 0 0 33.333333%;
      margin-bottom: 0;
    }

    @media (min-width: $xl) {
      flex: 0 0 25%;
    }
  }

  .review__info {
    display: flex;
    justify-content: space-between;
    height: 100%;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;

    @media (min-width: $md) {
      flex-direction: column;
      justify-content: flex-start;
    }

    @media (min-width: $xl) {
      justify-content: space-between;
    }

    @media (min-width: $xxl) {
      padding: 40px;
    }

    @media (min-width: 1700px) {
      padding: 60px;
    }
  }

  .review__control {
    display: none;

    @media (min-width: $md) {
      display: block;
    }
  }


  .review__next,
  .review__prev {
    display: none;

    @media (min-width: $md) {
      display: inline-flex;
    }
  }

  .review__title {
    font-weight: 700;
    line-height: 100%;
    text-transform: uppercase;

    @media (min-width: $md) {
      margin-bottom: 40px;
    }

    @media (min-width: $xl) {
      margin-bottom: 0;
    }
  }

  .review__next {
    margin-left: 6px;
  }

  .paging {
    font-family: var(--font-primary);
    font-weight: 500;
    margin-left: 10px;

    @media (min-width: $lg) {
      margin-left: 30px;
    }
  }

  .review__col-slider {
    @media (min-width: $md) {
      flex: 0 0 66.66667%;
      max-width: 66.66667%;
    }

    @media (min-width: $xl) {
      flex: 0 0 75%;
      max-width: 75%;
    }
  }

  .review__wrap-slider {
    height: 100%;
    margin-right: -10px;

    @media (min-width: $md) {
      margin-right: 0;
    }
  }

  .review__slider {

    & .slick-slide {
      margin: 0 2.5px;
      width: 91vw;
      padding: 15px;
      border-radius: 10px;
      background-color: #fff;
      transition: .4s ease 0s;

      @media (min-width: $md) {
        margin: 0;
        background-color: #fff;
      }

      @media (min-width: $xxl) {
        padding: 40px;
      }

      @media (min-width: 1700px) {
        padding: 60px;
      }
    }
  }


  .slide__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }

  .review__col-name {
    flex: 0 0 auto;
  }

  .review__slide-title {
    margin-bottom: 10px;
    font-family: var(--font-primary);
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;

    @media (min-width: $md) {
      font-size: 36px;
    }
  }

  .review__slide-name {
    font-weight: 500;
    font-family: var(--font-primary);
  }

  .review__slide-city {
    font-size: calc(11rem / 16);

    @media (min-width: $md) {
      font-size: calc(16rem / 16);
    }
  }

  .review__slide-car {
    flex: 0 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 37px;
    margin-left: 10px;
    border-radius: 30px;
    padding: 5px 24px;
    font-family: var(--font-primary);
    font-weight: 500;
    background-color: #EFF1F1;

    @media (min-width: $md) {
      height: 60px;
    }

    & span {
      color: var(--color-primary);
    }
  }
}



// ===== faq ===== 

@if 1 {

  .faq {
    text-align: center;
    color: #fff;
    border-radius: 10px;
    background-color: #003253;
    @include adaptive("padding-top", 160, 80, 1);
    @include adaptive("padding-bottom", 160, 80, 1);
  }

  .spoiler {
    position: relative;
    counter-reset: num1;
    list-style: none;

    &::after {
      content: "";
      position: absolute;
      left: -5px;
      right: -5px;
      top: 0;
      height: 1px;
      background-color: rgba(#fff, .6);
      display: block;

      @media (min-width: $md) {
        left: 5px;
        right: 5px;
      }
    }
  }

  .spoiler__item {
    position: relative;
    transition: .4s ease 0s;
    border-radius: 10px;
    margin: 0 -10px;

    @media (min-width: $md) {
      margin: 0;
    }

    &::before {
      content: counter(num1, decimal-leading-zero)" /";
      counter-increment: num1;
      position: absolute;
      top: 21px;
      left: 10px;
      font-weight: 500;

      @media (min-width: $md) {
        top: 45px;
        // left: 0px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 5px;
      right: 5px;
      bottom: 0;
      height: 1px;
      background-color: rgba(#fff, .6);
      display: block;
    }

    &.active {
      color: #003253;
      background-color: #EFF1F1;
    }

    @media (pointer: fine) {
      &:hover {
        color: #003253;
        background-color: #EFF1F1;
      }
    }
  }



  .spoiler__btn {
    position: relative;
    width: 100%;
    padding: 20px 40px 20px 40px;
    font-weight: 500;
    @include adaptive("font-size", 36, 18, 1);
    font-family: var(--font-primary);

    @media (min-width: $md) {
      padding: 40px 40px 40px 50px;
    }

    &::before {
      position: absolute;
      right: 7px;
      top: 21px;
      font-size: 20px;
      color: #EFF1F1;
      transition: .4s ease 0s;

      @media (min-width: $md) {
        top: 52px;
      }
    }

    &.active {
      color: var(--color-primary);

      &::before {
        color: #003253;
        transform: rotateX(180deg);
      }
    }

    @media (pointer: fine) {
      &:hover::before {
        color: #003253;
      }
    }
  }

  .spoiler__panel {
    display: none;
    max-width: 1100px;
    margin: 0 auto;
    padding: 0px 10px 20px 10px;

    @media (min-width: $md) {
      padding: 0px 40px 40px 50px;
    }
  }
}



// ===== report ===== 

@if 1 {

  .subtitle__col-report {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .report__wrap-slider {
    padding-bottom: 30px;
    padding-left: 15px;

    @media (min-width: $md) {
      padding-left: 0;
    }

    @media (min-width: $xl) {
      padding-bottom: 60px;
      margin: 0 -40px;
    }
  }

  .report__slider {

    & .slick-slide {
      width: 91vw;
      margin: 0 5px;
      transition: .4s ease 0s;

      @media (min-width: 1700px) {
        margin: 0 10px;
      }

      & img {
        width: 100%;
      }

      @media (min-width: $md) {}
    }

    & .slick-center {

      @media (min-width: $md) {
        width: 45vw !important;
      }

      @media (min-width: $xl) {
        width: 35vw !important;
      }
    }
  }

  .report__slide {
    & img {
      margin-bottom: 10px;
      border-radius: 10px;
    }
  }

  .report__slider-control {
    display: none;

    @media (min-width: $xl) {
      display: inline-flex;
    }
  }

  .report__next {
    margin-left: 10px;
  }

  .report__col-btn {
    display: flex;
    justify-content: center;
    padding: 0 15px;
  }

  .report__btn {
    width: 100%;
    border-radius: 30px;

    @media (min-width: $md) {
      min-width: 25vw;
      width: auto;
    }
  }
}