.head {
  padding: 10px 0px 10px 0px;
  @include adaptive("margin-bottom", 50, 40, 1);

  @media (min-width: $md) {
    padding: 30px 0px 30px 0px;
  }
  @media (min-width: $xxl) {
    padding: 30px 0px 30px 0px;
  }
}


.head__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo img {
  width: 200px;

  @media (min-width: $md) {
    width: auto;
  }
}

.head__img {
  width: 92px;

  @media (min-width: $md) {
    width: auto;
  }
}