// ==== General ==== 

html {
	height: 100%;
	width: 100%;
	font-size: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	height: 100%;
	width: 100%;
	min-width: 320px;
	// overflow-x: hidden;
	font-family: var(--font-second);
	font-size: calc(13rem / 16);
	line-height: 1.5;
	color: #1D1D1D;
	background-color: #EFF1F1;
	-webkit-tap-highlight-color: transparent;

	@media (min-width: $md) {
		font-size: calc(16rem / 16);
	}

	@media (min-width: $xl) {
		font-size: calc(20rem / 16);
	}
}

body.lock {
	overflow: hidden;
}


.wrapper {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: $maxWidth + px;
	min-height: 100%;
	margin: 0 auto;
	@media (min-width: $xl) {
		 padding: 0 40px;
	}
}


.main {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}


.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
}

.hidden {
	display: none !important;
}


p {
	margin-bottom: em(10);
}

.d-flex {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}