// ===== socials ===== 

.socials {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -5px;

  & li {
    margin: 5px;
  }

  & a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    font-size: 20px;
    color: #fff;
    border-radius: 50%;
    transition: .2s ease 0s;

    @media (min-width: $md) {
      width: 60px;
      height: 60px;
      font-size: 24px;
    }

    & img {
      display: block;
      width: 19px;
      height: 19px;

      @media (min-width: $md) {
        width: 24px;
        height: 24px;
      }
    }
  }

  @media (pointer: fine) {
    & a:hover {
      background-color: #003253 !important;
    }
  }

}