// ====  modal =====

.no-scroll {
  overflow-y: auto !important;
}

// body
.has-dialog {
  overflow: hidden;
}


// =====  ===== 

@if 1 {
  .dialog-backdrop {
    position: fixed;
    z-index: 100;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: auto;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden;
    transition: .3s ease 0s;

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }


  .modal {
    position: relative;
    // width: 100%;
    width: calc(100% - (10px * 2));
    max-width: 400px;
    max-height: 100%;
    transition: .3s ease 0s;
    transform: translate(0%, -20%);

    &.active {
      transform: translate(0%, 0);
    }

    @media (min-width: 460px) {
      width: calc(100% - (40px * 2));
      max-width: 670px;
    }
  }


  .modal__content {
    padding: 35px 15px;
    background-color: #EFEBFC;
    border-radius: 15px;
    overflow: auto;
    font-family: var(--font-primary);

    @media (min-width: $sm) {
      padding: 60px;
    }

    // @media (min-height: 500px) {
    //   max-height: 500px;
    // }

    // @media (min-height: 830px) {
    //   max-height: 780px;
    // }
  }

  .modal__close {
    position: absolute;
    top: 15px;
    right: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    color: rgba(#1D1D1D, .2);
    @media (min-width: $sm) {
      top: 20px;
      right: 20px;
    }
    
  }

}