// ===== title =====

.title-1,
.title-2,
.title-3,
.title-4,
.title-5,
.title-6 {
  font-family: var(--font-primary);
  font-weight: 700;
}


.title-1 {
  $title-1: 110;
  line-height: 1.1;
  @include adaptiveRem("font-size", $title-1, 40, 1);
  color: #1D1D1D;
  text-transform: uppercase;
}

.title-2 {
  $title-2: 70;
  line-height: 1.1;
  margin-bottom: 40/$title-2 * 1em;
  @include adaptiveRem("font-size", $title-2, 22, 1);
  text-transform: uppercase;

}

.title-3 {
  $title-3: 62;
  line-height: 1.1;
  font-weight: 500;
  @include adaptiveRem("font-size", $title-3, 26, 1);
}