// ===== footer ===== 

@if 1 {

	.footer {
		width: 100%;
		padding-bottom: 30px;
		@include adaptive("padding-top", 160, 80, 1);
		border-radius: 10px;
		color: #fff;
		background-color: #003253;

		@media (min-width: $md) {
			padding-bottom: 60px;
		}
	}


	.footer__row {

		margin-bottom: 30px;

		@media (min-width: $md) {
			margin-bottom: 60px;
		}

		@media (min-width: $xl) {
			display: flex;
			flex-wrap: wrap;
		}
	}

	.footer__col-contact {
		margin-bottom: 30px;
		order: 1;

		@media (min-width: $md) {
			margin-bottom: 0;
		}
	}

	.footer__col-title {
		margin-bottom: 20px;

		@media (min-width: $md) {
			flex: 0 0 41.666667%;
		}
	}

	.footer__title {
		margin-bottom: 10px;
		opacity: 0.5;
		font-family: var(--font-primary);
	}

	.footer__col-tel {

		@media (min-width: $md) {
			flex: 0 0 58.333333%;
		}
	}


	.footer__tel {
		margin-bottom: 10px;
		font-family: var(--font-primary);
		font-weight: 700;
		line-height: 110%;
		text-transform: uppercase;
		@include adaptive("font-size", 100, 40, 1);
		transition: .2s ease 0s;

		@media (min-width: $xl) {
			 margin-right: 40px;
		}

		@media (pointer: fine) {
			&:hover {
				color: #c0c0c0;
			}
		}
	}


	.footer__col-contact {
		@media (min-width: $md) {
			flex: 0 0 41.666667%;
		}
	}

	.footer__list {
		& address {
			font-style: normal;
		}

		& li {
			display: flex;
			align-items: center;

			&:not(:last-child) {
				margin-bottom: 10px;

				@media (min-width: $md) {
					margin-bottom: 30px;
				}
			}

			&::before {
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: 20px;
				border-radius: 10px;
				width: 60px;
				height: 60px;
				margin-right: 20px;
				background-color: #006EAD;
			}
		}

		& a {
			transition: .2s ease 0s;

			@media (pointer: fine) {
				&:hover {
					color: #c0c0c0;
				}
			}
		}
	}

	.footer__col-map {
		order: 1;

		@media (min-width: $md) {
			flex: 0 0 58.333333%;
		}

		& img {
			aspect-ratio: 1/0.42;
			object-fit: cover;
			border-radius: 10px;
		}
	}

	.copy {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		font-size: calc(13rem / 16);

		@media (min-width: $md) {
			font-size: calc(16rem / 16);
		}
	}

	.copy__link {
		display: flex;
		align-items: center;

		& img {
			margin-left: 10px;
		}
	}

	.copy__col {
		flex: 1 0 0%;
		padding-right: 10px;

		& br {
			@media (min-width: $md) {
				display: none;
			}
		}
	}

}


// ===== up ===== 

.btn-up {
	z-index: 5;
	position: fixed;
	right: 20px;
	bottom: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	pointer-events: none;
	width: 50px;
	height: 50px;
	color: #000;
	font-size: 20px;
	border-radius: 50%;
	// border: 1px solid rgba(29, 29, 29, 0.2);
	background-color: #fff;
	transform: rotate(-90deg);
	transition: .2s ease 0s;
	transition: .2s ease 0s;

	@media (min-width: $lg) {
		width: 60px;
		height: 60px;
	}

	@media (pointer: fine) {
		&:hover {
			background-color: #dadada;
		}
	}

	&.show {
		opacity: 1;
		pointer-events: visible;
	}
}