/* slick-Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}


.wrap-slider {
  position: relative;
}


.slick-track {
  display: flex !important;
}

.slick-slide {
  height: inherit !important;
}

.slide {
  display: flex;
  flex-direction: column;
  height: 100%;
}


.slider__next,
.slider__prev {
  // position: absolute;
  top: 46.5%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 60px;
  height: 60px;
  font-size: 18px;
  border-radius: 50%;

  &.slick-disabled {
    background-color: #C0C5D1;
    cursor: auto;
  }


}

.slider__prev {
  transform: rotate(180deg);
}

