// ===== btn ===== 

.btn {
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
  min-height: 50px;
  padding: 5px 30px;
  font-size: calc(16rem / 16);
  font-weight: 500;
  color: #ffffff;
  white-space: nowrap;
  // border: 2px solid #6D71F9;
  border-radius: 6px;
  // box-shadow: 6px 6px 12px rgba(213, 35, 49, 0.25);
  transition: .2s;

  &::before {
    margin-left: 10px;
  }

  @media (min-width: $md) {
    min-height: 60px;
    font-size: calc(20rem / 16);
  }

  & .icon {
    flex-shrink: 0;
  }

}


.btn-1 {
  background-color: #006EAD;

  @media (pointer: fine) {
    &:hover {
      // color: #587AFF;
      background-color: #4C90CD;
    }
  }

  &:active {
    // color: #002DE0;
    background-color: #4C90CD;
  }
}


.btn-2 {
  background-color: #3973E1;

  @media (pointer: fine) {
    &:hover {
      background-color: #FF7D30;
    }
  }

  &:active {
    background-color: #DE5203;
  }
}